<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isAuthenticated: false,
    };
  },
  created() {
    this.checkAuthentication();
  },
  methods: {
    checkAuthentication() {
      const token = localStorage.getItem('authToken');
      this.isAuthenticated = !!token; // トークンがあればログイン状態と判断
    },
  },
  watch: {
    '$route'() {
      this.checkAuthentication(); // ルートが変更されたときに認証状態を再チェック
    }
  }
};
</script>

