<template>
    <AppHeader />
    <div class="profile-container">
      <h1>プロフィール</h1>
      <div v-if="!isEditing">
        <p><strong>ユーザー名:</strong> {{ username }}</p>
        <p><strong>メールアドレス:</strong> {{ email }}</p>
        <!-- 他のプロフィール情報を表示 -->
        <button @click="editProfile">編集する</button>
      </div>
      <div v-else>
        <form @submit.prevent="updateProfile">
          <div class="input-group">
            <label for="username">ユーザー名</label>
            <input type="text" id="username" v-model="username" />
          </div>
          <div class="input-group">
            <label for="email">メールアドレス</label>
            <input type="email" id="email" v-model="email" />
          </div>
          <!-- 他のプロフィール情報を編集 -->
          <button type="submit">保存する</button>
          <button type="button" @click="cancelEdit">キャンセル</button>
        </form>
      </div>
    </div>
  </template>
  
  <script>
  import AppHeader from '@/components/AppHeader.vue';
  import axios from 'axios';
  
  export default {
    name: 'UserProfile',
    components: {
      AppHeader
    },
    data() {
        return {
            isLoggedIn: false,
            username: '',
            email: '',
            editing: false
        };
    },
    async mounted() {
      await this.fetchProfile();
    },
    methods: {
      async fetchProfile() {
        const token = localStorage.getItem('authToken');
        try {
          const response = await axios.get('http://localhost:8000/api/user/', {
            headers: {
              'Authorization': `Token ${token}`
            }
          });
          console.log('API Response:', response.data);
          this.username = response.data.username;
          this.email = response.data.email;
          // 他のプロフィール情報もここで設定
        } catch (error) {
          console.error('プロフィールの取得に失敗しました:', error);
        }
      },
      editProfile() {
        this.isEditing = true;
      },
      cancelEdit() {
        this.isEditing = false;
        this.fetchProfile(); // 編集前のデータを再取得
      },
      async updateProfile() {
        const token = localStorage.getItem('authToken');
        try {
          await axios.put('http://localhost:8000/api/user/', {
            username: this.username,
            email: this.email,
            // 他のプロフィール情報もここで送信
          }, {
            headers: {
              'Authorization': `Token ${token}`
            }
          });
          this.isEditing = false;
          alert('プロフィールが更新されました！');
        } catch (error) {
          console.error('プロフィールの更新に失敗しました:', error);
          alert('プロフィールの更新に失敗しました。再度お試しください。');
        }
      }
    }
  };
  </script>
  
